import request from '../service/api';

export function getNews(data) {
    return request({
        url: 'news/getNews',
        method: 'post',
        data
    })
}

export function getCateNews(data) {
    return request({
        url: 'news/GetCateNews',
        method: 'post',
        data
    })
}

export function getListNews(data) {
    return request({
        url: 'news/getListNews',
        method: 'post',
        data
    })
}

export function likeNews(data) {
    return request({
        url: 'news/likeNews',
        method: 'post',
        data
    })
}
export function Comment(data) {
    return request({
        url: 'news/createComment',
        method: 'post',
        data
    })
}
export function getListComment(data) {
    return request({
        url: 'news/getCommentByNews',
        method: 'post',
        data
    })
}
