<template>
  <div class="root">
  <span >{{ mininuteCount }}:</span>
    <span>{{ secondCount }}</span>

  </div>
</template>

<script>
  export default {
    name: "CountDown",
    props: ['mininutes'],
    data() {
      return {
        timer: null,
        mininuteCount: 0,
        secondCount: 59,
        done: false,
      }
    },
    mounted() {
      this.timer = setInterval(() => {
        if (!this.done) {
          this.secondCount--;
        }
      }, 1000)
    },
    watch: {
      mininutes: function(val){
        this.mininuteCount = (val > 0 ) ? val - 1 : 0;
      },
      secondCount: function (val) {
        let time = '';
        time = (this.mininutes - this.mininuteCount - 1) + ':' + (60 - val);

        if (val === 0 && this.mininuteCount === 0) {
          time = this.mininutes  + ':00';
          this.$emit('countDone');
          this.done = true;
        } else if (val === 0) {
          this.mininuteCount--;
          this.secondCount = 59;
        }
        this.$emit('getTime', time);
      },
      done: function (val) {
        if (val === true) {
          this.mininuteCount = 0;
          this.secondCount = 0;
        }
      }
    },

  }
</script>

