import request from '../service/api'

export function getListVideo(data) {
    return request({
        url: 'video/GetListByType',
        method: 'post',
        data
    })
}

export function getListType() {
    return request({
        url: 'video/getListType',
        method: 'post',
    })
}

export function GetListHot(data) {
    return request({
        url: 'video/GetListVideo',
        method: 'post',
        data
    })
}
