import Cookies from 'js-cookie'

const TokenCookies = 'Kien-Token';
const UserCookies = 'Kien-User';

/*
* Set Cookies
* */
export function setToken(token) {
    return Cookies.set(TokenCookies, token, { expires: 7 });
}

export function setCurrentUser(user) {
    user = JSON.stringify(user)
    return Cookies.set(UserCookies,user,{ expires: 7 })
}

/*
* Get Cookies
* */
export function getToken() {
    return Cookies.get(TokenCookies)
}

export function getCurrentUser() {
    let user = Cookies.get(UserCookies);
    if(user) {
        return JSON.parse(user)
    }else{
        return 1;
    }
}


/*
* remove Cookies
* */

export function removeToken(){
    return Cookies.remove(TokenCookies)
}

export function removeCurrentUser() {
    return Cookies.remove(UserCookies)
}