import request from '../service/api';

export function hotNews(data) {
    return request({
        url: 'home/getHotNews',
        method: 'post',
        data
    })
}

export function getListNewUser(data) {
    return request({
        url: 'home/getListNewsUser',
        method: 'post',
        data
    })
}

export function ListFeedback(data) {
    return request({
        url: 'feedback/getList',
        method: 'post',
        data
    })
}
