<template>
    <div>
        <section v-if="!finishTest">
            <section class="text-center" v-if="step == 0">
                <h3>Mã Đề: {{ testIdNumber('S',detail.test_id)}}</h3>
                <h4>Số Câu hỏi: {{ detail.number_question}}</h4>
                <h4>Level: {{ detail.level}}</h4>
                <b-button v-on:click="step = 1">Vào Thi</b-button>
            </section>
            <section v-if="step > 0">

                <hr>
                    <testing-component
                            :id="id"
                            :questions="detail.list_question"
                            :test-time="time"
                    ></testing-component>
                <div align="center">
                    <b-button v-if="check == false" v-on:click="step++">Đã Hiểu</b-button>
                </div>
            </section>
        </section>
        <section v-if="finishTest" class="text-center">
           <h3>Thí Sinh: {{ userInfo.name}}</h3>
            <h4>Mã Đề: {{ testIdNumber(detail.test_id)}}</h4>
            <h5>Kết Quả: {{ result }}</h5>
            <h5>Thời gian hoàn thành: {{ time }} </h5>
        </section>
    </div>
</template>

<script>
    import CountDown from "../components/Timing/CountDown";
    import QuestionComponent from "./QuestionComponent";
    import TestingComponent from "../components/Tests/TestingComponent";

    export default {
        name: "DoTest",
        components: {
            QuestionComponent,
            CountDown,
            TestingComponent
        },
        computed: {
            id: function () {
                return this.$route.query.test_id;
            },
            result: function () {
                let per = this.numberRight/this.detail.number_question * 100;
                return this.numberRight+'/'+this.detail.number_question + '('+per+'%)';
            }
        },
        mounted() {
            this.firstLoad();
        },
        data() {
            return {
                detail: [],
                numberQuestion: 0,
                step: 0,
                time: 10,
                finishTest: false,
                check: null,
                chooseAnswer: null,
                numberRight: 0,
            }
        },
        watch: {
            step: function (val) {
                this.check = null;
                if (val == this.detail.number_question) {
                    this.finishTest = true;
                }
            }
        },
        methods: {
            firstLoad() {
                let form = [];
                form['test_id'] = this.id;
                this.$store.dispatch('test/getDetail', form).then(response => {
                    if (response) {
                        console.log(response);
                        this.time = response.time;
                        this.detail = response;
                        let detail = [];
                        detail['name'] = 'Mã Đề ' + this.testIdNumber('S',response.test_id);
                        this.$emit('getDetail', detail);
                    } else {
                        this.$router.push('/error?code=404')
                    }
                }).catch(err => {
                    //console.log(err)
                })
            },
            checkAnswer() {
                let check = Boolean(this.chooseAnswer);
                this.check = check;
                if (this.chooseAnswer == null) {
                    alertify.error('Bạn Chưa chọn câu trả lời!');
                }
                if (check) {
                    this.step++;
                    this.chooseAnswer = null;
                    this.numberRight++;
                } else {
                }
                //this.check = null;
            },
            done() {
                this.finishTest = true;
            }
        }
    }
</script>

