import {createResult,getListByUser} from "../../api/result";

const state = {}

const mutations = {}

const actions = {
    //user login
    createResult({commit}, formData) {
        return new Promise((resolve, reject) => {
            createResult(formData).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListByUser({commit}, formData) {
        return new Promise((resolve, reject) => {
            getListByUser(formData).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
