<template>
    <div align="center">
        <b-img :src="background" fluid alt="Responsive image"></b-img>
    </div>
</template>

<script>
    export default {
        name: "ErrorPage",
        computed: {
            errorCode: function () {
                return this.$route.query.code;
            }
        },
        data() {
            return {
                background: '',
            }
        },
        mounted() {
            console.log(this.errorCode);
            this.background = require('../../assets/img/errors/'+this.errorCode+'.png');

        }
    }
</script>

