<script>
    import {Tabs, TabPane} from '@/components';
    import ListResult from "./Profile/ListResult";

    export default {
        name: 'profile',
        bodyClass: 'profile-page',
        components: {
            Tabs,
            TabPane,
            ListResult
        },
        data() {
            return {
                userDetail: null,

            }
        },
        computed: {
            id: function () {
                return (this.$route.query.id) ? this.$route.query.id : this.userInfo.id;
            },
            formLoad: function () {
                return {
                    id: this.id
                }
            }
        },
        created() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
               this.$store.dispatch('user/getProfile', this.formLoad).then(response => {
                   this.userDetail = response;
                });
            }
        }
    };
</script>
