import {create, } from "../../api/feedback";

const state = {}

const mutations = {}

const actions = {
    create({commit}, formData) {
        let {content, name} = formData;
        return new Promise((resolve, reject) => {
            create({content: content, name:name}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
