import {getDetail, getList} from "../../api/synthetic";

const state = {}

const mutations = {}

const actions = {
    //user login
    getList({commit}, formData) {
        let {page, size} = formData;
        return new Promise((resolve, reject) => {
            getList({page:page, size:size}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getDetail({commit}, formData) {
        let {id} = formData;
        return new Promise((resolve, reject) => {
            getDetail({id:id}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
