<template>
    <div class="root">

        <h2 class="">Say something to us:</h2>

        <b-form-group
                label="Name:"
        >
            <b-form-input v-model="name"></b-form-input>
        </b-form-group>
        <b-form-group
                label="Message:"
        >
            <tinymce id="d1" v-model="content"></tinymce>

        </b-form-group>
        <b-form-group>
            <b-button variant="success" v-on:click="submitForm"><i class="now-ui-icons arrows-1_share-66"></i> Send
            </b-button>
        </b-form-group>
    </div>
</template>

<script>
    import tinymce from 'vue-tinymce-editor';

    export default {
        name: "CreateFeedback",
        components: {
            tinymce,
        },
        data() {
            return {
                content: null,
                name: null,
            }
        },
        computed: {
            formData: function () {
                return {
                    content: this.content,
                    name: this.name,
                }
            }
        },
        methods: {
            submitForm() {
                this.$store.dispatch('feedback/create', this.formData).then(() => {
                    console.log('ok');
                })
            }
        }
    }
</script>

