<template>
    <div class="root">
        <b-row>
            <b-col sm="6" md="6">
                <h2 class="">Thành Viên Mới:</h2>
            </b-col>
            <b-col sm="6" md="6" align="right">
                <router-link class="" to="#">xem thêm...</router-link>
            </b-col>
        </b-row>
        <b-row>
            <b-col sm="12" md="3"
                   v-for="(item, index) in listUsers" :key="index"
            >
                <b-card
                        tag="article"
                        style="max-width: 20rem;"
                        class="mb-2"
                >
                    <b-avatar variant="info" :src="item.avatar" size="6rem"></b-avatar>
                    <h4>{{ item.name }}</h4>
                    <h5>Học Viên</h5>

                    <router-link :to="'/profile.html?id='+item.id" class="btn btn-outline-info">View Profile</router-link>
                </b-card>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    export default {
        name: "NewUsers",
        data() {
            return {
                listUsers: [],
            }
        },
        created() {
            this.firstLoad()
        },
        methods: {
            firstLoad(){
                this.$store.dispatch('home/getListNewUser').then(response => {
                    this.listUsers = response.lists;
                })
            }
        }
    }
</script>

