import {getNews, getCateNews, getListNews, likeNews, Comment, getListComment} from "../../api/news";


const state = {}

const mutations = {}

const actions = {
    //user login
    getNews({commit}, formData) {
        let { slug } = formData;
        return new Promise((resolve, reject) => {
            getNews({slug: slug}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    getCateNews({commit}, formData) {
        return new Promise((resolve, reject) => {
            getCateNews({}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListNews({commit}, slug) {
        return new Promise((resolve, reject) => {
            getListNews({slug: slug}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    likeNews({commit}, formData) {
        let { news_id } = formData;
        return new Promise((resolve, reject) => {
            likeNews({news_id: news_id}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    Comment({commit}, formData) {
        let { news_id, content } = formData;
        return new Promise((resolve, reject) => {
            Comment({news_id: news_id, content: content}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListComment({commit}, formData) {
        let { news_id, page } = formData;
        return new Promise((resolve, reject) => {
            getListComment({news_id: news_id, page: page}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
