<template>
    <div class="">
        <b-row>
            <b-col sm="12" v-if="totalPage > 1">
                <n-pagination
                        type="primary"
                        :page-count="totalPage"
                        v-model="currentPage"
                >
                </n-pagination>
            </b-col>
            <b-col sm="12" md="4"
                   v-for="(item, index) in listNews" :key="index"
            >
                <news-component
                        :show-cate="false"
                        :news="item"
                ></news-component>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import NewsComponent from "./NewsComponent";

    export default {
        name: "ListCateNews",
        components: {
            NewsComponent
        },
        data() {
            return {
                listNews: [],
                currentPage: 1,
                totalPage: 0,
                showCate: false,
            };
        },
        created() {
            this.firstLoad();
        },
        computed: {
            slug: function () {
                let detail = [];
                let slug = '';
                if (this.$route.params.slug != 'hot-list.html') {
                    detail['name'] = this.$route.params.name;
                    slug = this.$route.params.slug;
                    this.showCate = false;
                } else {
                    detail['name'] = 'List Hot News';
                    this.showCate =true;
                }
                this.$emit('getDetail', detail);
                return slug;
            },
        },
        watch: {
            slug: function (val) {
                this.firstLoad();
            },
            currentPage: function () {
                this.firstLoad();
            }
        },
        methods: {
            firstLoad() {
                this.$store.dispatch('news/getListNews', this.slug).then(response => {
                    this.totalPage = response.total_page;
                    let {lists} = response;
                    this.listNews = lists;
                }).catch(err => {
                    console.log(err);
                });
            }
        }
    }
</script>

<style scoped>
    .news-item {
        height: 350px;
    }

    .news-img {
        height: 40%;
    }
</style>
