<template>
    <div class="root">
        <div class="" v-if="totalPage >1">
            <kien-paginate v-model="currentPage" :total-page="totalPage" @handler="firstLoad"></kien-paginate>

        </div>
        <div class="table-responsive table-bordered">
            <table class="table table-hover">
                <thead class="thead-light">
                <tr>
                    <th v-for="(col) in table.collumns">{{ col }}</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item, index) in table.data">
                    <slot v-for="(columnIndex, indexColumn) in table.index">
                        <td :key="indexColumn" v-html="tableIndex(index, columnIndex)"></td>
                    </slot>
                    <td>
                        <b-dropdown split
                                    split-variant="outline-primary"
                                    variant="primary" id="dropdown-1" size="sm"
                                    text="Actions" class="m-md-2">
                            <b-dropdown-item variant="primary" v-on:click="showDetail(index)">
                                <i class="fa fa-edit"></i>
                                View Detail
                            </b-dropdown-item>

                        </b-dropdown>
                    </td>
                </tr>
                </tbody>
                <tfoot class="thead-light">
                <tr>
                    <th v-for="(col) in table.collumns">{{ col }}</th>
                </tr>
                </tfoot>
            </table>
        </div>
        <test-review v-if="showReview"
                     @closeModal="showReview = $event"
                     :answers="answerReview"
        />
    </div>
</template>

<script>
    import KienPaginate from "../components/Kien/KienPaginate";
    import TestReview from "../tests/TestReview";

    const table_collumns = [
        '#',
        'Học Viên',
        'Mã đề',
        'Điểm',
        'Thời gian',
        'Actions'
    ];
    const table_index = [
        '#',
        'user',
        'code',
        'point',
        'time',
    ];
    export default {
        name: "ListResult",
        props: ['id'],
        components: {
            KienPaginate,
            TestReview
        },
        computed: {
            formData: function () {
                return {
                    id: this.id,
                    size: 10,
                    page: this.currentPage,
                }
            }
        },
        data() {
            return {
                currentPage: 1,
                totalPage: 0,
                size: 10,
                table: {
                    collumns: [...table_collumns],
                    index: [...table_index],
                    data: []
                },
                showReview: false,
                answerReview: [],
            }
        },
        created() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
                this.$store.dispatch('result/getListByUser', this.formData).then(response => {
                    this.totalPage = response.total_page;
                    if (response.lists.length > 0) {
                        this.table.data = response.lists;
                    }
                })
            },
            tableIndex(index, key) {
                let item = this.table.data[index];
                switch (key) {
                    case table_index[0]: {
                        return this.size * (this.currentPage - 1) + index + 1;
                    }
                    case table_index[1]: {
                        return item[key].name;
                    }
                    case table_index[2]: {
                        return item[key] + item.id;
                    }
                    default: {
                        return item[key];
                    }
                }
            },
            showDetail(index){
                let obj = this.table.data[index];
                console.log(obj.answer_detail);
                this.answerReview = obj.answer_detail;
                this.showReview = true;
            }
        }
    }
</script>

