import request from '../service/api'

export function search(data) {
    return request({
        url: 'searchWord?word='+data.word,
        method: 'get',
        data
    })
}
export function searchList(data) {
    return request({
        url: 'searchList?word='+data.word,
        method: 'get',
        data
    })
}
