import {getListVideo, getListType, GetListHot} from "../../api/video";

const state = {}

const mutations = {}

const actions = {
    getListVideo({commit}, formData) {
        let {slug, page} = formData;
        return new Promise((resolve, reject) => {
            getListVideo({slug: slug, page: page}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListType({commit}, ) {
        return new Promise((resolve, reject) => {
            getListType({}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    GetListHot({commit}, formData) {
        let {size, page} = formData;
        return new Promise((resolve, reject) => {
            GetListHot({size: size, page: page}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
