<template>
    <div class="status">
        <b-button class="status-item" :class="class_liked" variant="primary" v-on:click="likeSubmit">
            <i class="now-ui-icons ui-2_favourite-28"></i>&ensp;
            <span>{{ numberLike }} Likes</span>
        </b-button>
        <b-button class="status-item" variant="primary">
            <i class="now-ui-icons ui-2_favourite-28"></i>&ensp;
            <span>0 Commnent</span>
        </b-button>
    </div>
</template>

<script>
    export default {
        name: "NewsBar",
        props: {
            id: {
                type: Number,
                default: 0,
            },
            liked: {
                type: Boolean,
                default: false,
            },
            number_like: {
                type: Number,
                default: 0,
            },
            number_comment: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                class_liked: null,
                isLike: false,
                numberLike: null,
            }
        },
        mounted() {
            this.isLike = this.liked;
            this.numberLike = this.number_like;
        },
        watch:{
            isLike: function(val){
                if (val == true) {
                    this.class_liked =  'text-primary';
                } else {
                    this.class_liked =  '';
                }
            }
        },
        methods: {
            likeSubmit() {
                let form = [];
                form['news_id'] = this.id;
                this.$store.dispatch('news/likeNews', form).then(response => {
                    console.log(response)
                    this.numberLike = response.number;
                    this.isLike = !this.isLike;
                })
            }
        }
    }
</script>

<style scoped>
    .status ul li {
        list-style: none;
        float: left;
        margin-right: 1%;
    }

    .status-item {
        border: none;
        background: none;
        color: black;
    }

    .status .status-item i {
        font-size: 12px;
    }
</style>