<template>
    <div class="">
        <div align="center"  v-if="!done">
            Time:
            <count-down
                    v-if="!done"
                    :mininutes="timeCount"
                    @countDone="finishSubmit"
                    @getTime="time = $event"></count-down>
            <hr>
            <h4>Listening Part</h4>
            <div v-for="(item,index) in listening" :key="index" class="text-left">
                <section v-if="item.name">
                    <h4>{{ item.name }}</h4>
                    <p v-html="item.describe"></p>

                </section>
                <section v-if="item.audio">
                    <audio controls>
                        <source :src="item.audio" type="audio/mpeg">
                    </audio>
                    <hr>
                </section>
                <section v-for="(question, index) in item.questions" :key="index">
                    <question-component
                            :question-detail="question"
                            :number-question="index+1"
                            @chooseAnswer="checkAnswer($event)"
                            :explain="check"
                    ></question-component>
                    <hr>
                </section>
            </div>
            <h4>Reading Part</h4>
            <div v-for="(item,index) in reading" :key="index">
                <section v-if="item.name">
                    <h4>{{ item.name }}</h4>
                    <p v-html="item.describe"></p>

                </section>
                <section v-if="item.audio">
                    <audio controls>
                        <source :src="item.audio" type="audio/mpeg">
                    </audio>
                    <hr>
                </section>
                <section v-for="(question, index) in item.questions" :key="index">
                    <question-component
                            :question-detail="question"
                            :number-question="index+1"
                            @chooseAnswer="checkAnswer($event)"
                            :explain="check"
                    ></question-component>
                    <hr>
                </section>

            </div>
            {{ numberRight }}
            <!-- done -->
            <section align="center">
                <b-button variant="success" v-on:click="finishSubmit">Done</b-button>
            </section>
        </div>
        <section v-if="done" class="text-center">
            <h3>Thí Sinh: {{ userInfo.name}}</h3>
            <h4>Mã Đề: {{ testIdNumber('T',id)}}</h4>
            <h5>Kết Quả: {{ point }}</h5>
            <h5>Thời gian hoàn thành: {{ time }} </h5>
        </section>
    </div>
</template>

<script>
    import CountDown from "../../components/Timing/CountDown";
    import QuestionComponent from "../QuestionComponent";

    export default {
        name: "ReadingDetail",
        components: {
            CountDown,
            QuestionComponent
        },
        computed: {
            id: function () {
                return this.$route.query.id;
            },
            point: function () {
                return this.numberRight+'/'+this.numberQuestions;
            }
        },
        mounted() {
            this.firstLoad();
        },
        data() {
            return {
                reading: [],
                listening: [],
                time: 0,
                done: false,
                timeCount: 0,
                numberRight: 0,
                numberQuestions: 0,
            }

        },
        methods: {
            firstLoad() {
                let form = [];
                form['id'] = this.id;
                this.$store.dispatch('synthetic/getDetail', form).then(response => {
                    console.log(response)
                    this.timeCount = parseInt(response.time);
                    this.reading = response.reading;
                    this.listening = response.listening;
                    this.numberQuestions = response.number_question;
                    let detail = [];
                    detail['name'] = 'Mã Đề ' + this.testIdNumber(response.code,response.id);
                    this.$emit('getDetail', detail);
                }).catch(err => {
                    console.log(err)
                })
            },
            chooseAnswer(kq, index) {
                this.answers[index]['status'] = kq;
            },
            finishSubmit() {
                this.done = true;
            },
            checkAnswer(detail) {
                if(detail.status == true){
                 this.numberRight++
                }
            },
        }
    }
</script>

<style scoped>

</style>