<template>
    <div class="">
        <b-row>
            <b-col sm="12" v-if="totalPage >1">
                <n-pagination
                        type="primary"
                        :page-count="totalPage"
                        v-model="currentPage"
                >
                </n-pagination>
            </b-col>

            <b-col sm="12" md="4"
                   v-for="(test, index) in data" :key="index"
            >
                <b-card :title="'Mã Đề '+ testIdNumber('S', test.test_id)" align="center" header-tag="header" footer-tag="footer">
                    <b-card-text>{{ test.number_question }} câu hỏi</b-card-text>
                    <b-button :to="'/testing.html?test_id='+test.test_id" variant="primary">Vào thi</b-button>
                </b-card>
            </b-col>
        </b-row>

    </div>
</template>

<script>
    import Pagination from "../../components/Pagination";
    export default {
        name: "ListTests",
        components:{
            [Pagination.name]: Pagination,
        },
        data() {
            return {
                totalPage: 0,
                currentPage: 0,
                data: [],
            }
        },
        created() {
            this.firstLoad();
        },
        watch:{
          currentPage: function (val) {

            this.firstLoad();
          }
        },
        methods: {
            firstLoad() {
                let form = [];
                form['page'] = this.currentPage;
                form['level_id'] = 1;
                this.$store.dispatch('test/getListByLevel', form).then(response => {
                    console.log(response);
                    this.totalPage = response.total_page;
                    this.data = response.lists;
                    let detail = [];
                    detail['name'] = 'Simple Questions';
                    this.$emit('getDetail', detail);
                }).catch(err => {
                    console.log(err);
                });
            }
        }
    }
</script>

