<template>
    <div class="root" :class="background">
        <div class="page-header " :class="defaultClass" filter-color="orange">
            <parallax
                    class="page-header-image"
                    :style="'background-image:url('+ coverImage +')'"
            >
            </parallax>
            <div class="container">
                <div class="content-center brand">
                    <h1 class="h1-seo">{{ namePage }}</h1>
                </div>
            </div>
            <div class="container">

                <h6 class="category category-absolute">
                    Designed by

                    <a href="https://www.facebook.com/kienchidong" target="_blank">
                        Hà Đức Kiên
                    </a>
                </h6>
            </div>

        </div>
        <div class="container" :class="contentClass">
            <router-view @getDetail="getDetail($event)"></router-view>
        </div>

    </div>
</template>

<script>
    import {logout} from "../api/api/user";

    export default {
        name: "DashbroadLayout",
        bodyClass: 'index-page',
        data() {
            return {
                namePage: (this.$route.name !== 'index') ? this.$route.name: '',
                coverImage: 'img/header1.jpg',
            }
        },
        computed: {
            defaultClass: function () {
                return (this.$route.name === 'index') ? '' : 'page-header-small';
            },
            contentClass: function () {
                return (this.$route.name === 'index') ? '' : 'content';
            },
            background: function () {
                return (this.$route.name === 'index') ? '' : 'background';
            },
        },
        mounted() {

        },
        methods:{
            getDetail(detail){
                this.namePage = (detail.name) ? detail.name : '';
                this.coverImage = (detail.cover) ? detail.cover : 'img/header1.jpg';
                console.log(this.namePage)
            }
        }
    }
</script>

<style scoped>
    .content {
        min-height: 25em;
        margin-top: -5%;
        background-color: white;
        border-radius: 20px;
        z-index: 2;
        position: relative;
        box-shadow: 10px 0px 20px 0px;
        padding: 2% 3%;
    }

    .background {
        background-image: url("../assets/img/background.jpg");
    }
</style>
