import request from '../service/api'

export function login(data) {
    return request({
        url: 'login',
        method: 'post',
        data
    })
}
export function logout(data) {
    return request({
        url: 'logout',
        method: 'post',
        data
    })
}

export function register(data) {
    return request({
        url: 'users/register',
        method: 'post',
        data
    })
}

export function getProfile(data) {
    return request({
        url: 'users/profile',
        method: 'post',
        data
    })
}
