<template>
    <div class="root">
        <b-row>
            <b-col sm="12" v-if="totalPage > 1">
                <n-pagination
                        type="primary"
                        :page-count="totalPage"
                        v-model="currentPage"
                >
                </n-pagination>
            </b-col>
            <b-col sm="12" md="4"
                   v-for="(item, index) in listVideo" :key="index"
            >
                <video-component
                        :show-cate="false"
                        :video="item"
                ></video-component>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import VideoComponent from "./VideoComponent";
    export default {
        name: "ListVideo",
        components:{
            VideoComponent
        },
        data(){
          return {
              currentPage: 1,
              totalPage: 0,
              formLoad: {
                  slug: null,
                  page: null,
              },
              listVideo: [],
          }
        },
        created() {
            this.firstLoad();
        },

        computed: {
            slug: function () {
                let detail = [];
                let slug = '';
                if (this.$route.params.slug != 'hot-list.html') {
                    detail['name'] = this.$route.params.name;
                    slug = this.$route.params.slug;
                    this.showCate = false;
                } else {
                    detail['name'] = 'List Hot Video';
                    this.showCate =true;
                }
                this.$emit('getDetail', detail);
                return slug;
            },
        },
        watch: {
            slug: function (val) {
                this.firstLoad();
            },
            currentPage: function () {
                this.firstLoad();
            }
        },
        methods:{
            firstLoad(){
                this.formLoad.slug =this.slug;
                this.formLoad.page =this.currentPage;
                this.$store.dispatch('video/getListVideo', this.formLoad).then(response => {
                   this.totalPage = response.total_page;
                   this.listVideo = response.lists;
                });
            }
        }
    }
</script>

