<template>
    <div class="root">
        <b-row>
            <b-col sm="12" v-if="totalPage > 1">
                <kien-paginate v-model="currentPage" :total-page="totalPage" @handler="firstLoad"></kien-paginate>
            </b-col>

            <b-col sm="12" md="4"
                   v-for="(test, index) in data" :key="index"
            >
                <b-card :title="'Mã Đề '+ testIdNumber(test.code,test.id)" align="center" header-tag="header" footer-tag="footer">
                    <b-card-text>{{ test.name }}</b-card-text>
                    <b-button :to="'/detail-synthetic-test.html?id='+test.id" variant="primary">Vào thi</b-button>
                </b-card>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import KienPaginate from "../../components/Kien/KienPaginate";
    export default {
        name: "ReadingTest",
        components: {KienPaginate},
        data() {
            return {
                currentPage: 1,
                totalPage: 1,
                data: [],
                size: 9,
            }
        },
        computed: {
            formLoad: function () {
                return {
                    page: this.currentPage,
                    size: this.size,
                }
            }
        },
        created() {
            this.firstLoad()
        },
        methods:{
            firstLoad(){
                this.$store.dispatch('synthetic/getList', this.formLoad).then(response =>{
                    console.log(response)
                    this.totalPage = response.total_page;
                    this.data = response.lists;
                })
            }
        }
    }
</script>

