<template>
    <div class="root">
        <b-row>
            <b-col sm="6" md="6">
                <h2 class="">Hot Videos:</h2>
            </b-col>
            <b-col sm="6" md="6" align="right">
                <router-link class="" :to="{name: 'video', params:{slug: 'hot-list.html'}}">xem thêm...</router-link>
            </b-col>
        </b-row>
        <b-row>
            <b-col sm="12" md="3"
                   v-for="(item, index) in listVideos" :key="index"
            >
                <video-component
                        :show-cate="true"
                        :video="item"
                ></video-component>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import VideoComponent from "./VideoComponent";
    export default {
        name: "HotVideos",
        components:{
            VideoComponent
        },
        data() {
            return {
                formData: {
                    size: 4,
                    page: 1
                },
                listVideos: [],
            }
        },
        created() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
                console.log('abc')
                this.$store.dispatch('video/GetListHot', this.formData).then(response => {
                    this.listVideos = response.lists;
                })
            }
        }
    }
</script>

