<template>
    <n-pagination
            type="primary"
            :page-count="TotalPage"
            v-model="page"
    >
    </n-pagination>
</template>

<script>
    import Pagination from "../../../components/Pagination";

    export default {
        name: "KienPaginate",
        components: {
            [Pagination.name]: Pagination,
        },
        props: {
            TotalPage: {
                type: Number,
                default: 0,
            },
            value: {
                type: Number,
                required: true
            }
        },
        computed: {
            page: {
                get() {
                    return this.value
                },
                set(page) {
                    this.$emit('input', page)
                    this.$emit('handler')
                }
            }
        },
    }
</script>

