<template>
    <div class="root">
        <b-card img-alt="Image" img-top>
            <div v-if="showCate">
                <router-link :to="{name: 'cateNews', params:{slug: news.cate_slug}}"  class="small text-muted">{{ news.cate }}</router-link>
                <hr>
            </div>

            <b-card-img
                    height="120px"
                    :src="news.image"
                    class="news-img"
            ></b-card-img>
            <hr>
            <section style="min-height: 120px">
                <router-link :to="{name: 'News', params:{slug: news.slug}}"><h6>{{ news.title }}</h6></router-link>
                <span v-if="news.summary">
                    {{ subString(news.summary) }}
                </span>
            </section>
            <hr>
            <span :class="classLiked">{{ news.number_like }} likes</span>&nbsp;
            <span class="">{{ news.number_comment }} comments</span>
        </b-card>
    </div>
</template>

<script>
    export default {
        name: "NewsComponent",
        props: {
            news: {
                type: Object,
                require: true,
            },
            showCate: {
                type: Boolean,
                default: false,
            }
        },
        created() {

        },
        computed: {
            classLiked: function () {
                if (this.news.liked === true) {
                    return 'text-info';
                }
                return '';
            }
        }
    }
</script>

