<template>
    <div>
        <b-row>
            <b-col sm="6" md="6">
                <h2 class="">Tin Nổi Bật:</h2>
            </b-col>
            <b-col sm="6" md="6" align="right">
                <router-link class="" :to="{name: 'cateNews', params:{slug: 'hot-list.html'}}">xem thêm...</router-link>
            </b-col>
        </b-row>
        <b-row>
            <b-col sm="12" md="4"
                   v-for="(item, index) in listNews" :key="index"
            >
                <news-component
                        :show-cate="true"
                        :news="item"
                ></news-component>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import NewsComponent from "../news/NewsComponent";

    export default {
        name: "HotNews",
        components: {
            NewsComponent
        },
        data() {
            return {
                listNews: [],
            }
        },
        mounted() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
                this.$store.dispatch('home/hotNews').then(response => {
                    this.listNews = response.lists;
                }).catch(err => {

                })
            }
        }
    }
</script>

