<template>
    <div class="">
        <div v-if="!done">
            <div align="center">
                Thời gian:
                <count-down
                        v-if="!done"
                        :mininutes="testTime"
                        @countDone="finishSubmit"
                        @getTime="time = $event"></count-down>

            </div>

            <section v-if="name">
                <h4>{{ name }}</h4>
                <p v-html="describe"></p>

            </section>
            <section v-if="audio">
                <audio controls>
                    <source :src="audio" type="audio/mpeg">
                </audio>
                <hr>
            </section>
            <section v-for="(question, index) in questions" :key="index">
                <question-component
                        :question-detail="question"
                        :number-question="index+1"
                        @chooseAnswer="checkAnswer($event, index)"
                        :explain="check"
                ></question-component>
                <hr>
            </section>
        </div>
        <section v-if="done" class="text-center">
            <h3>Thí Sinh: {{ userInfo.name}}</h3>
            <h4>Mã Đề: {{ testIdNumber(code,id)}}</h4>
            <h5>Kết Quả: {{ point }}</h5>
            <h5>Thời gian hoàn thành: {{ time }} </h5>
        </section>
        <!-- done -->
        <section align="center">
            <b-button variant="success" v-if="!done" v-on:click="finishSubmit">Done</b-button>
            <b-button variant="success" v-if="done" v-on:click="ReView">ReView</b-button>
        </section>

        <!--modal review-->
        <test-review v-if="showReview"
                     @closeModal="showReview = $event"
                     :answers="answerReview"
        />
    </div>
</template>

<script>
    import QuestionComponent from "../../tests/QuestionComponent";
    import CountDown from "../Timing/CountDown";
    import TestReview from "../../tests/TestReview";

    export default {
        name: "TestingComponent",
        components: {
            QuestionComponent,
            CountDown,
            TestReview
        },
        props: {
            id: {
                type: String,
                default: null,
            },
            name: {
                type: String,
                default: null,
            },
            describe: {
                type: String,
                default: null,
            },
            questions: {
                type: Array,
                require: true,
            },
            audio: {
                type: String,
                default: null,
            },
            testTime: {
                default: 10
            }
        },
        data() {
            return {
                check: false,
                done: false,
                result: [],
                time: null,
                numberRight: 0,
                answerDetail: [],
                showReview: false,
                answerReview: []
            }

        },
        computed: {
            code: function () {
                if (this.name) {
                    return 'G';
                }
                return 'S';
            },
            point: function () {
                let per = this.numberRight / this.questions.length * 100;
                return this.numberRight + '/' + this.questions.length + '(' + per + '%)';
            },
            resultDetail: function () {
                return {
                    test_id: this.id,
                    point: this.point,
                    time: this.time,
                    is_simple: (!this.name) ? 1 : 0,
                    is_read: (this.name != null && this.audio == null) ? 1 : 0,
                    is_listen: (this.audio) ? 1 : 0,
                    answer_detail: JSON.stringify(this.answerDetail),
                }
            }
        },
        methods: {
            finishSubmit() {
                this.done = true;
                this.$store.dispatch('result/createResult', this.resultDetail).then(response => {
                    console.log(response);
                    this.answerReview = response.answer_detail;
                })
            },
            checkAnswer(detail, index) {

                this.result[index] = detail.status;
                this.answerDetail[index] = {
                    question_id: this.questions[index].id,
                    chose_id: detail.id
                };
                this.numberRight = 0;
                this.result.forEach((value, index) => {
                    if (value == 1) {
                        this.numberRight++
                    }
                })
            },
            ReView() {
                this.showReview = true;
            }
        }
    }
</script>

