import {login, logout, register, getProfile} from '../../api/user';
import {getToken, getCurrentUser, setToken, setCurrentUser, removeToken, removeCurrentUser} from "../../../utils/auth";

const state = {
    token: getToken(),
    userCurrent: {}
}

const mutations = {
    SET_TOKEN: (state, token) => {
        state.token = token
        setToken(token)
    },
    SET_USER: (state, user) => {
        state.user = user
        setCurrentUser(user)
    },
}

const actions = {
    login({commit}, formData) {
        const {email, password} = formData
        return new Promise((resolve, reject) => {
            login({email: email.trim(), password: password}).then(reponse => {
                if (reponse) {
                    const {data} = reponse;
                    alertify.success('Xin chào ' + data.user.name + ' !!');
                    commit('SET_TOKEN', data.token);
                    commit('SET_USER', data.user);
                    resolve();
                }
            }).catch(error => {
                reject(error)
            })
        })
    },
    logout({commit},) {
        return new Promise((resolve, reject) => {
            logout({}).then(reponse => {
                if (reponse) {
                    removeToken();
                    removeCurrentUser();
                    resolve()
                }
            }).catch(error => {
                reject(error)
            })
        })
    },
    register({commit}, data) {
        let {name, email, password, repassword, avatar} = data;

        return new Promise((resolve, reject) => {
            register({
                name: name,
                email: email,
                password: password,
                repassword: repassword,
                avatar: avatar
            }).then(reponse => {
                if (reponse) {
                    const {data} = reponse;
                    resolve(data)
                }
            }).catch(error => {
                reject(error)
            })
        })
    },
    getProfile({commit}, data) {
        let {id} = data;
        return new Promise((resolve, reject) => {
            getProfile({id:id}).then(reponse => {
                if (reponse) {
                    const {data} = reponse;
                    resolve(data)
                }
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
