<template>
    <div class="root">
        <p v-html="newsDetail.content"></p>

        <a href="#" v-for="(tag, index) in newsDetail.tag" :key="index">#{{ tag.name }} </a>
        <hr>
        <div class="status">
            <b-button class="status-item" :class="class_liked" pill variant="primary" v-on:click="likeSubmit">
                <i class="now-ui-icons ui-2_favourite-28"></i>&ensp;
                <span>{{ newsDetail.number_like }} Likes</span>
            </b-button>
            <b-button class="status-item" pill variant="primary" v-on:click="getListComment">
                <i class="now-ui-icons ui-2_favourite-28"></i>&ensp;
                <span>{{ newsDetail.number_comment }} Commnent</span>
            </b-button>
        </div>
        <hr>

        <section>
            <b-card v-for="(comment, index) in listComment" :key="index">
                <b-media>
                    <template v-slot:aside>
                        <b-avatar badge badge-variant="success" :src="comment.user_create.avatar"
                                  class="mr-3"></b-avatar>
                    </template>
                    <h6 class="mt-0">{{ comment.user_create.name }}</h6>
                    <span>{{ comment.content }}</span>
                </b-media>
            </b-card>
        </section>
        <hr>
        <section>
            <b-row>
                <b-col sm="12" md="10">
                    <b-input v-model="content"></b-input>
                </b-col>
                <b-col sm="12" md="2">
                    <b-button pill variant="primary" v-on:click="CommentSubmit">
                        Gửi
                    </b-button>
                </b-col>
            </b-row>

        </section>
    </div>
</template>

<script>
    import NewsBar from "../components/News/NewsBar";

    export default {
        name: "NewsDetail",
        components: {
            NewsBar
        },
        data() {
            return {
                formData: {
                    slug: this.$route.params.slug,
                },
                newsDetail: {},
                class_liked: false,
                content: null,
                listComment: [],
                page_comment: 1,
            };
        },
        created() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
                this.$store.dispatch('news/getNews', this.formData).then(response => {
                    this.newsDetail = response;
                    console.log(response)
                    let detail = []
                    detail['name'] = response.title;
                    detail['cover'] = response.image;
                    if (response.liked == true) {
                        this.class_liked = 'text-info';
                    } else {
                        this.class_liked = '';
                    }
                    this.$emit('getDetail', detail);
                    this.getListComment();
                })
            },
            likeSubmit() {
                let form = [];
                form['news_id'] = this.newsDetail.id;
                this.$store.dispatch('news/likeNews', form).then(response => {
                    this.firstLoad();
                })
            },
            CommentSubmit() {
                let form = [];
                form['news_id'] = this.newsDetail.id;
                form['content'] = this.content;
                this.$store.dispatch('news/Comment', form).then(response => {
                    this.newsDetail.number_comment++;
                    this.listComment.push(response);
                    this.content = null;
                })
            },
            getListComment() {
                let form = [];
                form['news_id'] = this.newsDetail.id;
                form['page'] = this.page;
                this.$store.dispatch('news/getListComment', form).then(response => {
                    this.page = response.next_page;
                    response.lists.forEach((item) => {
                        this.listComment.push(item)
                    })
                })
            },

        }
    }
</script>

<style scoped>
    .status ul li {
        list-style: none;
        float: left;
        margin-right: 1%;
    }

    .status-item {
        border: none;
        background: none;
        color: black;
    }

    .status .status-item i {
        font-size: 12px;
    }

    .comment {
        border: red solid 1px;
        min-width: 900px;
    }
</style>
