<template>
    <div class="">
        <section>
            <h3>{{ detailQuestion.name }}</h3>
            <p v-html="detailQuestion.describe"></p>
            <hr>
        </section>
        <section>
            <b-form-group
                    label="Content:"
            >
                <tinymce id="d1" v-model="content"></tinymce>

            </b-form-group>
        </section>
        <section>
            <b-button v-on:click="submitAction">Done</b-button>
        </section>
    </div>
</template>

<script>
    import tinymce from 'vue-tinymce-editor';
    export default {
        name: "WritingDetail",
        components:{
            tinymce
        },
        data() {
            return {
                detailQuestion: [],
                content: null,
            }
        },
        computed: {
            id: function () {
                return this.$route.query.group_id;
            },
            title: function () {
                return 'Writing For '+this.detailQuestion.name;
            },
            formData: function () {
                return {
                    title: this.title,
                    content: this.content,
                    question_id: this.id
                }
            }
        },
        mounted() {
            this.firstLoad();
        },
        methods: {
            firstLoad() {
                let form = [];
                form['group_id'] = this.id;
                this.$store.dispatch('test/getDetailReading', form).then(response => {
                    console.log(response)
                    this.detailQuestion = response;
                }).catch(err => {
                    //console.log(err)
                })
            },
            submitAction(){
                this.$store.dispatch('test/answerWriting', this.formData).then(response => {
                    console.log(response)
                })
            }
        }
    }
</script>

