import { hotNews, getListNewUser, ListFeedback } from "../../api/home";


const state = {}

const mutations = {}

const actions = {
    //user login
    hotNews({commit}, ) {
        return new Promise((resolve, reject) => {
            hotNews({}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListNewUser({commit}, ) {
        return new Promise((resolve, reject) => {
            getListNewUser({}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    ListFeedback({commit}, ) {
        return new Promise((resolve, reject) => {
            ListFeedback({}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
