<template>
    <div class="root">
        <b-row>
            <b-col sm="6" md="6">
                <h2 class="">Phản Hồi TỪ Học viên:</h2>
            </b-col>
        </b-row>
        <b-row>
            <b-col sm="12" md="3"
                   v-for="(item, index) in listFeed" :key="index"
            >
                <b-card img-alt="Image" img-top>
                    <b-avatar variant="info" :src="item.user.avatar" size="6rem"></b-avatar>
                    <h4>{{ item.user.name }}</h4>
                    <h5>Học Viên</h5>
                    <section style="min-height: 120px">
                        <p v-html="item.content"></p>
                    </section>
                    <hr>
                </b-card>
            </b-col>
        </b-row>

    </div>
</template>

<script>
    export default {
        name: "ListFeedback",
        data(){
            return{
                listFeed: []
            }
        }, created() {
            this.firstLoad();
        },
        methods:{
            firstLoad(){
                this.$store.dispatch('home/ListFeedback').then(response => {
                    console.log(response)
                    this.listFeed = response.lists;
                })
            }
        }
    }
</script>

