<template>
    <div class="root">
        <b-modal hide-footer v-model="show">
            <b-form>
                <b-row>
                    <b-col sm="9">
                        <b-form-input list="search-list" placeholder="word" v-model="valueSearch"></b-form-input>
                        <b-form-datalist id="search-list" :options="option"></b-form-datalist>
                    </b-col>
                    <b-col sm="3">
                        <b-button variant="primary" class="btn-round" v-on:click="searchSubmit">Search</b-button>
                    </b-col>
                </b-row>

            </b-form>
            <div>
                <h4>{{ item.word }}</h4>
                <hr>
                <p v-html="item.detail"></p>
            </div>

        </b-modal>
    </div>
</template>

<script>
    export default {
        name: "SearchWord",
        data() {
            return {
                show: false,
                valueSearch: null,
                formSearch: {
                    word: '',
                },
                option: [],
                item: {
                    like: null,
                    word: null,
                    detail: null,
                    same: [],
                }

            }
        }, mounted() {
            this.show = true;
        },
        watch: {
            valueSearch: function (val) {
                this.formSearch.word = val;
                this.searchList();
            },
            show: function (val) {
                if (val == false) {
                    this.$emit('closeModal');
                }
            }
        },
        methods: {
            searchList() {
                this.search('dictionary/searchList');
            },
            searchSubmit() {
                this.search('dictionary/search');
            },
            search(type) {
                this.$store.dispatch(type, this.formSearch).then(response => {
                    if (response.word) {
                        this.item.like = (response.like) ? response.like : null;
                        this.item.word = response.word.word;
                        this.item.detail = response.word.detail;
                    } else {
                        this.option = response;
                    }
                })
            }
        }

    }
</script>

