<template>
    <div>
        <b-row>
            <b-col sm="12" md="6">

                <b-form-group
                        label="Name(*):"
                >
                    <b-form-input
                            v-model="name"
                            type="text"
                            required
                            placeholder="Enter your Name"
                    ></b-form-input>
                </b-form-group>
                <b-form-group
                        label="Email address:"
                        description="We'll never share your email with anyone else."
                >
                    <b-form-input
                            v-model="email"
                            type="email"
                            required
                            placeholder="Enter email"
                    ></b-form-input>
                </b-form-group>
                <b-form-group
                        label="Password:"
                >
                    <b-form-input
                            v-model="password"
                            type="password"
                            required
                            placeholder="Enter password"
                    ></b-form-input>
                </b-form-group>
                <b-form-group
                        label="Confirm Password:"
                >
                    <b-form-input
                            v-model="rePassword"
                            type="password"
                            required
                            placeholder="Confirm Password"
                    ></b-form-input>
                </b-form-group>
            </b-col>
            <b-col sm="12" md="6">
                <b-form-group
                        label="Avatar:"
                >
                    <b-form-file
                            placeholder="Choose a file or drop it here..."
                            drop-placeholder="Drop file here..."
                            v-on:change="uploadFile"
                    ></b-form-file>
                </b-form-group>
                <img :src="avatar" width="100%" alt="">
            </b-col>
            <b-col sm="12" align="center">
                <b-button variant="success" v-on:click="registerUser">Register</b-button>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import {getCurrentUser} from "../../utils/auth";


    export default {
        name: "RegisterUser",
        data() {
            return {
                name: null,
                email: null,
                password: null,
                rePassword: null,
                avatar: null,
            }
        },
        computed: {
            formData: function () {
                let form = {
                    'name': this.name,
                    'email': this.email,
                    'password': this.password,
                    'repassword': this.rePassword,
                    'avatar': this.avatar,
                }
                return form;
            }
        },
        methods: {
            uploadFile(e) {
                let files = e.target.files || e.dataTransfer.files;
                let arrayType = ['image/jpeg', 'image/png', 'image/jpg'];
                if (!arrayType.includes(files[0].type)) {
                    e.target.value = '';
                } else {
                    let reader = new FileReader();
                    reader.onload = (e) => {
                        this.avatar = e.target.result;
                    }
                    reader.readAsDataURL(files[0]);
                }
            },
            registerUser(){
                this.$store.dispatch('user/register', this.formData).then(response => {
                    this.$store.dispatch('user/login', this.formData).then(() => {
                        this.Logined = true;
                        this.userInfo = getCurrentUser();
                        this.$router.push('/index')
                    })
                }).catch(err => {

                })
            }
        }
    }
</script>

<style scoped>

</style>
