import Vue from 'vue';
import Router from "vue-router";
import Index from "../pages/Index";
import MainNavbar from "../layout/MainNavbar";
import MainFooter from "../layout/MainFooter";
import Login from "../pages/Login";
import index2 from "../pages/index2";
import Landing from "../pages/Landing";
import abc from "../pages/abc";
import DashbroadLayout from "../layout/DashbroadLayout";
import ListCateNews from "../pages/news/ListCateNews";
import NewsDetail from "../pages/news/NewsDetail";
import ListTests from "../pages/tests/ListTests";
import DoTest from "../pages/tests/DoTest";
import ErrorPage from "../pages/Errors/ErrorPage";
import RegisterUser from "../pages/Users/RegisterUser";
import ListVideo from "../pages/video/ListVideo";
import ReadingTest from "../pages/tests/Reading/ReadingTest";
import ReadingDetail from "../pages/tests/Reading/ReadingDetail";
import ListeningTest from "../pages/tests/Listening/ListeningTest";
import Profile from "../pages/Profile";
import WritingTest from "../pages/tests/Writing/WritingTest";
import WritingDetail from "../pages/tests/Writing/WritingDetail";
import SyntheticTest from "../pages/tests/Synthetic/SyntheticTest";
import SyntheticDetail from "../pages/tests/Synthetic/SyntheticDetail";


Vue.use(Router);

const routes = [
    {
        path: '/',
        name: 'dashbroad',
        redirect: 'index.html',
        components: {default: DashbroadLayout, header: MainNavbar, footer: MainFooter},
        props: {
            header: {colorOnScroll: 400},
        },
        children: [
            {
                path: "index.html",
                name: "index",
                component: Index,
                meta: {
                    permissions: "all",
                },
            },
            {
                path: "/cateNews/:slug",
                name: "cateNews",
                component: ListCateNews,
                props: true,
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },
            {
                path: "/news/:slug",
                name: "News",
                component: NewsDetail,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },
            {
                path: "/Simple-test.html",
                name: "List Test",
                component: ListTests,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                }
            },
            {
                path: "/testing.html",
                name: "Test",
                component: DoTest,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    mustLogin: true,
                    title: 'title'
                }
            },
            {
                path: "/list-reading-test.html",
                name: "Reading Test",
                component: ReadingTest,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    type: 0
                }
            },
            {
                path: "/list-listening-test.html",
                name: "Listening Test",
                component: ListeningTest,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    type: 1
                }
            },
            {
                path: "/list-writing-test.html",
                name: "Writing Test",
                component: WritingTest,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    type: 1,
                }
            },
            {
                path: "/detail-reading-test.html",
                name: "Reading Detail",
                component: ReadingDetail,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    mustLogin: true,
                }
            },
            {
                path: "/detail-writing-test.html",
                name: "Writing Detail",
                component: WritingDetail,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    mustLogin: true,
                }
            },
            {
                path: "/register.html",
                name: "Register User",
                component: RegisterUser,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },{
                path: "/error",
                name: "Error",
                component: ErrorPage,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },
            {
                path: "/video/:slug",
                name: "video",
                component: ListVideo,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },

            {
                path: "/List-synthetic-test.html",
                name: "Synthetic Test",
                component: SyntheticTest,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title'
                }
            },

            {
                path: "/detail-synthetic-test.html",
                name: "Synthetic Test",
                component: SyntheticDetail,
                props: {
                    showBanner: false,
                },
                meta: {
                    permissions: "all",
                    title: 'title',
                    mustLogin: true,
                }
            },
        ]
    }, {
        path: '/index2',
        name: 'index2',
        components: {default: index2, header: MainNavbar, footer: MainFooter},
        props: {
            header: {colorOnScroll: 500},
            footer: {backgroundColor: 'black'}
        },
    },{
        path: '/profile.html',
        name: 'profile',
        components: {default: Profile, header: MainNavbar, footer: MainFooter},
        props: {
            header: {colorOnScroll: 500},
            footer: {backgroundColor: 'black'}
        },
    },
    {
        path: '/login',
        name: 'login',
        components: {default: Login, header: MainNavbar},
        props: {
            header: {colorOnScroll: 400}
        }
    },
    {
        path: '/landing',
        name: 'landing',
        components: {default: Landing, header: MainNavbar, footer: MainFooter},
        props: {
            header: {colorOnScroll: 400},
            footer: {backgroundColor: 'black'}
        },
    },
];

export default routes;

