import {getListByLevel, getDetail, getListReading, getDetailReading, answerWriting} from "../../api/test";

const state = {}

const mutations = {}

const actions = {
    getListByLevel({commit}, formData) {
        let { level_id, page } = formData;
        return new Promise((resolve, reject) => {
            getListByLevel({level_id: level_id, page:page}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getDetail({commit}, formData) {
        let { test_id } = formData;
        return new Promise((resolve, reject) => {
            getDetail({test_id: test_id}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getListReading({commit}, formData) {
        let { page, type } = formData;
        return new Promise((resolve, reject) => {
            getListReading({page: page, type:type}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    getDetailReading({commit}, formData) {
        let { group_id } = formData;
        return new Promise((resolve, reject) => {
            getDetailReading({group_id: group_id}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    answerWriting({commit}, formData) {
        let { title, content, question_id } = formData;
        return new Promise((resolve, reject) => {
            answerWriting({title: title, content:content, question_id:question_id}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
