<template>
    <div class="">
        <testing-component
                :id="id"
                :name="name"
                :describe="describe"
                :questions="questions"
                :audio="audio"
                :test-time="time"
        ></testing-component>

    </div>
</template>

<script>
    import TestingComponent from "../../components/Tests/TestingComponent";

    export default {
        name: "ReadingDetail",
        components: {
            TestingComponent
        },
        computed: {
            id: function () {
                return this.$route.query.group_id;
            },
        },
        mounted() {
            this.firstLoad();
        },
        data() {
            return {
                name: null,
                describe: null,
                questions: [],
                answers: [],
                check: false,
                audio: null,
                time: 0,
            }

        },
        methods: {
            firstLoad() {
                let form = [];
                form['group_id'] = this.id;
                this.$store.dispatch('test/getDetailReading', form).then(response => {
                    console.log(response)
                    this.name = response.name;
                    this.describe = response.describe;
                    this.questions = response.questions;
                    this.audio = response.audio;
                    this.time = response.time;
                }).catch(err => {
                    //console.log(err)
                })
            },
            chooseAnswer(kq, index) {
                this.answers[index]['status'] = kq;
            }
        }
    }
</script>

