import {search, searchList} from "../../api/dictionary";

const state = {}

const mutations = {}

const actions = {
    //user login
    search({commit}, formData) {
        let {word} = formData;
        return new Promise((resolve, reject) => {
            search({word: word}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
    searchList({commit}, formData) {
        let {word} = formData;
        return new Promise((resolve, reject) => {
            searchList({word: word}).then(reponse => {
                const {data} = reponse;
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },
}
export default {
    namespaced: true,
    state,
    mutations,
    actions
}
