            >
                <nav-link :to="{name: 'video', params:{slug: 'hot-list.html'}}">
                    Tất cả Video
                </nav-link>
                <nav-link :to="{name: 'video', params:{slug: item.slug, name: item.name}}" v-for="(item, index) in ListType" :key="index">
                    {{ item.name }}
                </nav-link>
            </drop-down>
            <drop-down
                    tag="li"
                    title="Tests"
                    icon="now-ui-icons education_paper"
                    class="nav-item"
            >
                <nav-link to="/Simple-test.html">
                    Simple test
                </nav-link>
                <nav-link to="/list-reading-test.html">
                    Reading test
                </nav-link>
                <nav-link to="/list-listening-test.html">
                    Listening test
                </nav-link>
                <nav-link to="/list-writing-test.html">
                    Writing test
                </nav-link>
                <nav-link to="/List-synthetic-test.html">
                    Synthetic test
                </nav-link>
            </drop-down>
            <li class="nav-item" >
                <a class="nav-link" href="#" v-on:click="ShowDictionary = !ShowDictionary">
                    <p>dictionary</p>
                </a>
            </li>
            <drop-down
                    tag="li"
                    title="News"
                    icon="now-ui-icons design_image"
                    class="nav-item"
            >
                <nav-link :to="{name: 'cateNews', params:{slug: 'hot-list.html'}}">
                    Tất cả Tin
                </nav-link>
                <nav-link :to="{name: 'cateNews', params:{slug: item.slug, name: item.name}}" v-for="(item, index) in CateNews" :key="index">
