                </nav-link>
            </drop-down>
            <drop-down
                    tag="li"
                    :title="userInfo.name"
                    class="nav-item"
                    v-if="Logined"
            >
                <nav-link to="/profile.html">
                    <i class="now-ui-icons users_single-02"></i> Profile
                </nav-link>
                <a class="dropdown-item" href="#" v-on:click="logoutUser">
                    <i class=""></i> Logout
                </a>
            </drop-down>

            <search-word v-if="ShowDictionary" @closeModal="ShowDictionary = !ShowDictionary"></search-word>
            <li class="nav-item" v-if="!Logined">
                <a class="nav-link" href="#" v-on:click="modal = !modal">
                    <p>Login</p>
                </a>
            </li>
            <li class="nav-item" v-if="!Logined">
                <a class="nav-link">
                    /
                </a>
            </li>
            <li class="nav-item" v-if="!Logined">
                <router-link class="nav-link" to="/register.html" >
                    <p>Register</p>
                </router-link>
            </li>
           <!-- <li class="nav-item">
                <a
                        class="nav-link"
                        rel="tooltip"
                        title="Like us on Facebook"
                        data-placement="bottom"
                        target="_blank"
                >
                    <i class="fab fa-facebook-square"></i>
                    <p class="d-lg-none d-xl-none">Facebook</p>
                </a>
            </li>-->
            <b-modal v-model="modal" title="Login">
                <label >Email:</label>
                <b-form-input
                        placeholder="Enter your name"
                        type="email"
                        trim
                        v-model="formLogin.email"
                ></b-form-input>
                <label >Password:</label>
                <b-form-input
                        placeholder="Enter your name"
                        type="password"
                        trim
                        v-model="formLogin.password"
                ></b-form-input>
                <hr>
                <template v-slot:modal-footer>
                    <b-button v-on:click="Login">Login</b-button>
                </template>
            </b-modal>
        </template>
    </navbar>
</template>

<script>
    import {DropDown, NavbarToggleButton, Navbar, NavLink} from '@/components';
    import {Popover} from 'element-ui';
    import {getCurrentUser} from "../utils/auth";
    import SearchWord from "../pages/dictionary/SearchWord";

    export default {
        name: 'main-navbar',
        components: {
            DropDown,
            Navbar,
            NavbarToggleButton,
            NavLink,
            [Popover.name]: Popover,
            SearchWord
        },
        props: {
            transparent: Boolean,
            colorOnScroll: Number
        },
        data() {
            return {
                modal: false,
                formLogin: {
                    email: '',
                    password: ''
                },
                CateNews: [],
                ListType: [],
                ShowDictionary: false,
            }
        },
        mounted() {
            this.getCatenews();
            this.getListType();
        },
        methods:{
            Login() {
                this.$store.dispatch('user/login', this.formLogin).then(response => {
                    this.Logined = true;
                    this.userInfo = getCurrentUser();
                    this.modal = false;

                })
            },
            logoutUser() {
                this.$store.dispatch('user/logout')
                    .then(() => {
                        this.Logined = false;
                    })
            },
            getCatenews() {
                this.$store.dispatch('news/getCateNews', this.formData).then(response => {
                    this.CateNews = response.lists;
                })
            },
            getListType() {
                this.$store.dispatch('video/getListType', this.formData).then(response => {
                    this.ListType = response.lists;
                })
            }
        }

    };
</script>

